import React, { useState, useCallback } from 'react';
import { useFormik } from 'formik';
import { object, string } from 'yup';

import Input from './Input';
import Select from './Select';
import Error from './Error';
import Button from './Button';

import { fonts } from '../constants';
import { identifySegment, trackSegment } from '../utils/segment';
import { getCampaign } from '../utils/utm';

export default function BlockContactSalesForm() {
  const [error, setError] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);

  const onSubmit = useCallback(async (values, { setSubmitting, resetForm }) => {
    try {
      setError(null);
      const {
        name,
        email,
        phone,
        company,
        website,
        job_title,
        industry,
        annual_revenue,
        accounting_platform,
        customer_purchase_frequency,
        offer_net_terms,
        ecommerce_platform,
      } = values;
      values.campaign = getCampaign();

      identifySegment({
        email,
        phone,
        name,
        company: { name: company },
        website,
        job_title,
        industry,
        annual_revenue,
        accounting_platform,
        customer_purchase_frequency,
        offer_net_terms,
        ecommerce_platform,
      });
      trackSegment('Contact form submitted', {
        name,
        email,
        phone,
        company,
        website,
        context: {
          campaign: getCampaign(),
        },
        job_title,
        industry,
        annual_revenue,
        accounting_platform,
        customer_purchase_frequency,
        offer_net_terms,
        ecommerce_platform,
      });

      const host =
        window.location.hostname === 'localhost'
          ? 'http://localhost:2222'
          : 'https://app.resolvepay.com';

      const res = await fetch(`${host}/api/web/contact`, {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(values),
      }).then(body => body.json());

      if (res && res.success) {
        setSubmitting(false);
        resetForm();
        setShowSuccess(true);
      }
    } catch (err) {
      setSubmitting(false);
      setError(err);
    }
  }, []);

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
      company: '',
      website: '',
      job_title: '',
      industry: '',
      annual_revenue: '',
      accounting_platform: '',
      customer_purchase_frequency: '',
      offer_net_terms: '',
      ecommerce_platform: '',
    },
    validationSchema: object().shape({
      name: string().trim().required('Required'),
      email: string()
        .trim()
        .lowercase()
        .email('Invalid email')
        .required('Required'),
      phone: string().trim().required('Required'),
      company: string().trim().required('Required'),
      website: string().trim().lowercase().required('Required'),
      job_title: string().trim().required('Required'),
      industry: string().required('Required'),
      annual_revenue: string().required('Required'),
      accounting_platform: string().required('Required'),
      customer_purchase_frequency: string().required('Required'),
      offer_net_terms: string().required('Required'),
      ecommerce_platform: string(),
    }),
    onSubmit,
  });

  return (
    <div>
      {showSuccess ? (
        <div>
          <p className="contact_sales__subtitle">
            Thanks! We'll get back to you soon
          </p>
        </div>
      ) : (
        <div>
          <p className="contact_sales__subtitle">
            Tell us how to reach you and let's discuss how Resolve can grow your
            b2b business.
          </p>
          <div className="contact_sales_form__form-container">
            <form onSubmit={formik.handleSubmit}>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Input
                    id="name"
                    name="name"
                    error={formik.errors.name}
                    value={formik.values.name}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Full name"
                  />
                </div>
                <div className="resolve-col">
                  <Input
                    id="company"
                    name="company"
                    type="text"
                    error={formik.errors.company}
                    value={formik.values.company}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Company"
                  />
                </div>
              </div>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Input
                    id="email"
                    name="email"
                    error={formik.errors.email}
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Work email"
                  />
                </div>
                <div className="resolve-col">
                  <Input
                    id="phone"
                    name="phone"
                    type="tel"
                    error={formik.errors.phone}
                    value={formik.values.phone}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Phone"
                  />
                </div>
              </div>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Input
                    id="job_title"
                    name="job_title"
                    error={formik.errors.job_title}
                    value={formik.values.job_title}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Job title"
                  />
                </div>
                <div className="resolve-col">
                  <Input
                    id="website"
                    name="website"
                    type="text"
                    error={formik.errors.website}
                    value={formik.values.website}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Website"
                  />
                </div>
              </div>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Select
                    id="industry"
                    name="industry"
                    error={formik.errors.industry}
                    value={formik.values.industry}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Industry"
                    options={[
                      'Agriculture, Forestry, and Fishing',
                      'Auto',
                      'Construction',
                      'Education',
                      'Electronics',
                      'Fashion & Beauty',
                      'Financial Services',
                      'Fitness & Sporting Goods',
                      'Furniture',
                      'Healthcare',
                      'Industrial Tools & Equipment',
                      'Maintenance, Repair, and Operations (MRO)',
                      'Retail',
                      'Restaurant',
                      'Professional Services',
                      'Software',
                      'Travel',
                      'Transportation',
                      'Other',
                    ]}
                  />
                </div>
                <div className="resolve-col">
                  <Select
                    id="customer_purchase_frequency"
                    name="customer_purchase_frequency"
                    error={formik.errors.customer_purchase_frequency}
                    value={formik.values.customer_purchase_frequency}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Customer purchase frequency"
                    options={['Monthly', 'Quarterly', 'Annually', 'Once']}
                  />
                </div>
              </div>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Select
                    id="annual_revenue"
                    name="annual_revenue"
                    error={formik.errors.annual_revenue}
                    value={formik.values.annual_revenue}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Annual B2B revenue"
                    options={[
                      'Pre-launch',
                      '<$1M',
                      '$1M-$5M',
                      '$5M-$10M',
                      '$10M-$25M',
                      '>$25M',
                    ]}
                  />
                </div>
                <div className="resolve-col">
                  <Select
                    id="offer_net_terms"
                    name="offer_net_terms"
                    error={formik.errors.offer_net_terms}
                    value={formik.values.offer_net_terms}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Do you offer net terms?"
                    options={[
                      'Not at all',
                      '1-25% of b2b revenue',
                      '25-50% of b2b revenue',
                      '>50% of b2b revenue',
                    ]}
                  />
                </div>
              </div>
              <div className="resolve-row clearfix">
                <div className="resolve-col">
                  <Select
                    id="accounting_platform"
                    name="accounting_platform"
                    placeholder="Accounting platform"
                    error={formik.errors.accounting_platform}
                    value={formik.values.accounting_platform}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="Accounting platform"
                    options={[
                      'Freshbooks',
                      'Microsoft Dynamics 365',
                      'Netsuite',
                      'Odoo',
                      'Oracle',
                      'Quickbooks Desktop',
                      'Quickbooks Enterprise',
                      'Quickbooks Online',
                      'Sage',
                      'SAP',
                      'Wave',
                      'Xero',
                      'Zoho',
                      'Other',
                    ]}
                  />
                </div>
                <div className="resolve-col">
                  <Select
                    id="ecommerce_platform"
                    name="ecommerce_platform"
                    placeholder="E-commerce platform"
                    error={formik.errors.ecommerce_platform}
                    value={formik.values.ecommerce_platform}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    label="E-commerce platform"
                    options={[
                      '3dCart',
                      'BigCommerce',
                      'Custom',
                      'Hybris',
                      'Insite',
                      'Magento 1',
                      'Magento 2',
                      'Netsuite',
                      'None',
                      'NuOrder',
                      'Odoo',
                      'Oro Commerce',
                      'Shopify',
                      'Shopify Plus',
                      'SquareSpace',
                      'Volusion',
                      'WooCommerce',
                      'Other',
                    ]}
                  />
                </div>
              </div>

              <Error
                error={
                  error &&
                  'There was an error submitting your request. Please try again.'
                }
              />
              <Button
                type="submit"
                primary
                disabled={formik.isSubmitting}
                className="contact_sales_form__submit"
              >
                Submit
              </Button>
            </form>
          </div>
        </div>
      )}
      <style jsx>{`
        .clearfix::after {
          content: '';
          clear: both;
          display: table;
        }
        .contact_sales__subtitle {
          font-family: ${fonts.halyardDisplay};
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
          margin: 20px 0 40px;
          text-align: center;
        }
        .resolve-row {
          margin-left: -15px;
          margin-right: -15px;
        }
        .resolve-col {
          float: left;
          width: 50%;
          padding: 0 15px;
        }
        @media (max-width: 767px) {
          .resolve-col {
            float: none;
            width: 100%;
          }
        }
      `}</style>
      <style jsx global>{`
        .contact_sales_form__submit {
          width: 100%;
        }
        .contact_sales__container * {
          box-sizing: border-box;
        }
      `}</style>
    </div>
  );
}
