import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Container from '../components/Container';
import BlockContactSalesForm from '../components/BlockContactSalesForm';
import Footer from '../components/Footer';
import BlockLogos from '../components/BlockLogos';
import SpaceDivider from '../components/SpaceDivider';

import { fonts } from '../constants';
import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;

export default function ContactSalesPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <GatsbySeo title="Contact sales" />
      <Container>
        <div className="contact_sales__container">
          <div className="contact_sales__title">See Resolve in action</div>
          <BlockContactSalesForm />
        </div>
        <SpaceDivider height={100} />
        <BlockLogos
          pretitle="Trusted by innovative companies"
          data={[
            {
              image: companies['Vice Golf'],
              name: 'Vice Golf',
              offsetTop: 0,
            },
            {
              image: companies['Article'],
              name: 'Article',
            },
            {
              image: companies['OnDemand Tires'],
              name: 'OnDemand Tires',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 0,
            },
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 0,
            },
            {
              image: companies['QuietKat'],
              name: 'QuietKat',
              offsetTop: 0,
            },
          ]}
        />
      </Container>
      <Footer darkSkin />
      <style jsx>{`
        .contact_sales__container {
          max-width: 600px;
          width: 100%;
          margin: 65px auto;
        }
        .contact_sales__container * {
          box-sizing: border-box;
        }
        .contact_sales__title {
          font-family: ${fonts.halyardDisplay};
          font-size: 28px;
          font-weight: 600;
          line-height: 1.43;
          letter-spacing: 0px;
          text-align: center;
        }
      `}</style>
    </Layout>
  );
}
